@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  color: #191919;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  background-color: #e5e5e5;
}

@media (min-width: 2100px) {
  body {
    zoom: 1.2;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  margin: 0 auto;
  padding: 0 60px;
}

@media (max-width: 1366px) {
  .site-container {
    padding: 0 40px;
  }
}

@media (max-width: 1200px) {
  .site-container {
    padding: 0 35px;
  }
}

@media (max-width: 992px) {
  .site-container {
    padding: 0 24px;
  }
}

@media (max-width: 480px) {
  .site-container {
    padding: 0 16px;
  }
}

.page-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.button-reset {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.burger-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 40px;
  cursor: pointer;
}

.burger-menu span {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #fff;
  border-radius: 5px;
}

.burger-menu span:nth-of-type(2) {
  margin: 5px 0;
}

.logo .icon-factory {
  width: 50px;
  height: 50px;
  fill: #191919;
}

.button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline: none;
  cursor: pointer;
}

.button-primary {
  position: relative;
  z-index: 5;
  padding: 21px 62px;
  overflow: hidden;
  color: #191919;
  font-size: 1.125rem;
  border: 2px solid #191919;
  border-radius: 5px;
  transition: color 0.3s ease-in-out;
}

@media (min-width: 1024px) {
  .button-primary:hover {
    color: #fff;
  }
}

.button-primary__hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  height: 100%;
  overflow: hidden;
  background-color: #191919;
  transition: 0.3s ease-in-out;
  pointer-events: none;
}

.button:active {
  transform: scale(0.99);
}

.button__play {
  position: absolute;
  top: 50%;
  left: 30px;
  z-index: 150;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 116px;
  background-color: #f4bf4b;
  border: 1px solid #e5e5e5;
  border-radius: 20px;
  transform: translateY(-50%);
  cursor: pointer;
}

.button__play .icon {
  width: 25px;
  height: 25px;
  opacity: 0.6;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  fill: #e5e5e5;
}

.button__play:hover .icon {
  transform: scale(1.2);
  opacity: 1;
}

.header {
  padding: 24px 0;
}

.header__container {
  display: flex;
}

.header__burger {
  position: relative;
  z-index: 50;
  margin-left: auto;
}

.header__burger:hover span {
  background-color: #f4bf4b;
}

.hero .video {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  min-width: 100%;
  min-height: 100%;
  line-height: 1.2;
  transform: translate(-50%, -50%);
}

.site-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 45;
  width: 100%;
  height: 100%;
}

.site-menu__container {
  position: absolute;
  top: 0;
  right: 0;
  width: 35%;
  height: 100%;
  padding: 60px 45px;
  background-color: #fff;
}

.site-menu__list {
  margin: 0;
  padding: 0;
}

.site-menu__item {
  list-style: none;
}

.site-menu__link {
  color: inherit;
  font-weight: 700;
  font-size: 3.5rem;
  text-decoration: none;
}

.section {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
}

.section ._text-container {
  max-width: 950px;
  margin: 0 auto;
}

.section__container {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.section__container .simplebar-track.simplebar-vertical {
  width: 5px;
}

.section__container .simplebar-scrollbar::before {
  right: 0;
  left: auto;
  width: 1px;
}

.section__aside {
  position: absolute;
  top: 0;
  right: 0;
  width: 70%;
  height: 100%;
  padding: 32px 56px;
  background-color: #fff;
  transform: translateX(100%);
  transition: transform 0.7s ease-in-out;
}

.section._active {
  z-index: 15;
}

.section._active .section__aside {
  transform: translateX(0);
}

.section__tags {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;
  margin-bottom: 50px;
}

.section__tags span {
  display: inline-block;
  margin: 5px;
  transition: color 0.2s ease-in-out;
}

.section__tags span:hover {
  color: #f4bf4b;
}

.section__heading {
  margin: 0;
  margin-bottom: 32px;
  font-size: 3.5rem;
}

.section__text {
  margin: 0;
  margin-bottom: 56px;
  font-size: 1.5rem;
  line-height: 1.3;
}

.section__img {
  position: relative;
  width: 90%;
  height: 450px;
  margin: 0 auto;
  margin-bottom: 56px;
  overflow: hidden;
}

.section__img .layer {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.section__img:nth-last-of-type(1) {
  margin-bottom: 0;
}

.section__buttons {
  display: flex;
  max-width: 90%;
  margin: 0 auto;
  padding-top: 32px;
  border-top: 1px solid #191919;
}

.main {
  color: #191919;
}

.footer {
  padding: 24px 0;
}

.footer__container {
  display: flex;
}

.footer__logo .icon-factory:hover {
  fill: #f4bf4b;
}
